.bg-lines {
  background: repeating-linear-gradient(
      -55deg,
      #222,
      #222 2px,
      #fff 4px,
      #fff 36px
  );
}

.bg-dotted {
  background: white;
  background-image: radial-gradient(black 1px, transparent 0);
  background-size: 40px 40px;
  background-position: -19px -19px;
  width: 100%;
  height: 100%;
}

.bg-polka {
  background-image: radial-gradient(dodgerblue 20%, transparent 20%), radial-gradient(skyblue 20%, transparent 20%);
  background-color: #fff;
  background-position: 0 0, 50px 50px;
  background-size: 100px 100px;
}

.bg-zig-zag {
  width: 240px;
  height: 240px;
  background-color: #fff;
  background-image: linear-gradient(135deg, #000 25%, transparent 25%),
    linear-gradient(225deg, #000 25%, transparent 25%),
    linear-gradient(315deg, #000 25%, transparent 25%),
    linear-gradient(45deg, #000 25%, transparent 25%);
  background-position: -30px 0, -30px 0, 0 0, 0 0;
  background-size: 60px 60px;
  background-repeat: repeat;
  }
